.artist__page_root{margin:0 auto;width:1000px;height:100vh;padding-top:100px;color:#fff;display:flex;flex-direction:column;align-items:center;background-image:linear-gradient(to top, #fff1eb 0%, #ec0303 100%)}.artist__page_root h1{color:#fff}.artist__like_icon{cursor:pointer;position:relative}.artist__like_icon:hover{transform:scale(1.1)}.artist__like_icon:hover::after{position:absolute;top:-10px;left:30px;width:200px;height:30px;border-radius:10px;background-color:rgba(131,0,0,.9);display:flex;justify-content:center;align-items:center;content:"Добавить в коллекцию";color:#fff}.artists__total_likes{display:flex;align-items:center;width:100px;justify-content:space-around;margin-top:30px;color:#000}.genre{cursor:pointer;background-image:linear-gradient(-20deg, #f9f9f9 0%, #efefef 100%);-webkit-box-shadow:1px 3px 5px 0px rgba(50,50,50,.75);-moz-box-shadow:1px 3px 5px 0px rgba(50,50,50,.75);box-shadow:1px 3px 5px 0px rgba(50,50,50,.75);cursor:pointer}.genre:hover{transform:scale(1.1);background-image:linear-gradient(-20deg, #f9f9f9 0%, #757474 100%)}.artist__main_icon{margin-top:30px;-webkit-box-shadow:4px 4px 19px 0px rgba(50,50,50,.75);-moz-box-shadow:4px 4px 19px 0px rgba(50,50,50,.75);box-shadow:4px 4px 19px 0px rgba(50,50,50,.75)}.artist__main_icon img{max-height:320px}.artists__genres_list{margin-top:30px}.artists__genres_list span{color:#24756a;font-weight:900;cursor:pointer}.artists__genres_list span:hover{transform:scale(1.05)}.albums__next_button{padding-top:30px;padding-bottom:30px;display:flex;justify-content:center;width:100%;background-color:#040404}.ref{color:#000}